/* style the items (options), including the selected item: */
.select-selected {
	cursor: pointer;
	width: 100%;
	height: rem(48);
	border-radius: rem(8);
	color: var(--steel);
	background-color: transparent;
	padding: 0 rem(14);
	border: rem(1) solid #e6eaef;

	display: flex;
	align-items: center;

	font-size: rem(16);
	font-family: var(--f-default);

	@include placeholder {
		color: var(--steel);
	}
}

.select-arrow-active {
	border-bottom-right-radius: 0;
	border-bottom-left-radius: 0;
}

/* Style items (options): */
.select-items {
	position: absolute;
	background-color: white;
	// border-radius: rem(8);
	// height: rem(40);
	top: 100%;
	left: 0;
	right: 0;
	z-index: 99;
	// margin-top: rem(8);

	div {
		cursor: pointer;
		width: 100%;
		height: rem(48);
		// border-radius: rem(5);
		color: var(--steel);
		background-color: white;
		padding: 0 rem(14);
		border-right: rem(1) solid #e6eaef;
		border-left: rem(1) solid #e6eaef;
		border-bottom: rem(1) solid #e6eaef;

		display: flex;
		align-items: center;

		font-size: rem(16);
		font-family: var(--f-default);

		box-shadow: 0 3px 12px 0 rgba(0, 0, 0, 0.12),
		0 22px 24px -5px rgba(0, 0, 0, 0.08);
	}
}

/* Hide the items when the select box is closed: */
.select-hide {
	display: none;
}

.select-items div:hover,
.same-as-selected {
	background-color: var(--cornflower);
	color: white;

	border-color: var(--cornflower);
}